define('../src/journeys/j01-register/j01-register',['jquery', 'parsley', 'lib/utils'], function($, parsley, utils) {

    'use strict';

    var J01 = function($journey) {

        this.$journey = $journey;

        return this;
    };

    J01.prototype.init = function() {
        var _this = this;


        _this.$journey.css('opacity', '1');

        if (!this.$journey.hasClass('stream')) {
            setTimeout(function() {
                if (_this.$journey.attr('isVirtualEvent').toLowerCase() == 'true') {
                    _this.$journey.find('.j01-register--next').hide();
                    _this.$journey.find('.button--submit').show();
                } else {
                    _this.$journey.find('.j01-register--next').show();
                    _this.$journey.find('.button--submit').hide();
                };
            }, 500)

            if (utils.breakpoint() == 'l') {
                this.$journey.find('.j01-register--form_wrapper').css('min-height', this.$journey.find('.j01-register--mainimg').outerHeight(true));
            } else {
                this.$journey.find('.j01-register--form_wrapper').css('min-height', '0');
            };

            $(window).off('resize.j01').on('resize.j01', function() {
                if (utils.breakpoint() == 'l') {
                    _this.$journey.find('.j01-register--form_wrapper').css('min-height', _this.$journey.find('.j01-register--mainimg').outerHeight(true));
                } else {
                    _this.$journey.find('.j01-register--form_wrapper').css('min-height', '0');
                };
            });


            this.form();
        } else {
            $('.box--girih').css('height', (($(window).outerHeight(true) - (54 + $('.box--girih').prev('.grid').outerHeight(true)))));
            //if (utils.breakpoint() != 's') {
                $('.j01-register--stream').css('height', ($('.box--girih').outerHeight(true) - 72));
                $('.j01-register--stream').css('width', (($('.box--girih').outerHeight(true) - 72) / 9) * 16);
                $('.j01-register--stream').css('max-height', (($('.j01-register--stream').width())/16)*9);
            //}
            $(window).off('resize.j01v2').on('resize.j01v2', function() {
                $('.box--girih').css('height', (($(window).outerHeight(true) - (54 + $('.box--girih').prev('.grid').outerHeight(true)))));
                //if (utils.breakpoint() != 's') {
                    $('.j01-register--stream').css('height', ($('.box--girih').outerHeight(true) - 72));
                    $('.j01-register--stream').css('width', (($('.box--girih').outerHeight(true) - 72) / 9) * 16);
                    $('.j01-register--stream').css('max-height', (($('.j01-register--stream').width())/16)*9);
                //}
            });
            $('body').addClass('unscrollable');
        }
    };

    J01.prototype.form = function() {
        var _this = this,
            $button = $('.j01-register--next');

        $button.off('click.check').on('click.check', function() {
            var $wrapper = $('.j01-register--form_group.active'),
                $inputs = $wrapper.find('.form-field__input'),
                errors = 0;

            errors = 0;
            $inputs.each(function() {

                if (!$(this).parsley().isValid()) {
                    errors++;
                };

                $(this).parsley().validate();
            });

            if (errors == 0) {
                $wrapper.fadeOut(400);
                $wrapper.removeClass('active');
                $wrapper.next('.j01-register--form_group').addClass('active');
                setTimeout(function() {
                    if ($wrapper.next('.j01-register--form_group').hasClass('last')) {
                        $('.button--submit').show();
                        $('.j01-register--next').hide();
                        $('.captcha').show();
                    };

                    $wrapper.next('.j01-register--form_group').fadeIn(400)
                }, 400);
            };
        });
    }

    return J01;
});

